@import "../../global.scss";

.testimonials {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include mobile {
        justify-content: space-around;
    }


    h1 {
        font-size: 50px;

        @include mobile {
            font-size: 20px;
        }
    }

    .container {
        width: 100%;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile {
            flex-direction: column;
            height: 100%;
        }

        .card {
            width: 250px;
            height: 70%;
            border-radius: 10px;
            box-shadow: 0px 0px 15px -8px black;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 20px;
            transition: all 1s ease;

            @include mobile {
                height: 200px;
                margin: 10px 0;
                padding: 5px;

            }

            &.featured {
                width: 300px;
                height: 75%;
                margin: 0 30px;

                @include mobile {
                    width: 250px;
                    height: 180px;
                    margin: 1px;
                }
            }

            &:hover {
                transform: scale(1.1);
            }

            .top {
                display: flex;
                align-items: center;
                justify-content: center;
                padding-bottom: 2px;
                overflow: hidden;

                img {

                    &.left,
                    &.right {
                        height: 25px;
                    }

                    &.user {
                        height: 60px;
                        width: 60px;
                        border-radius: 50%;
                        object-fit: cover;
                        margin: 0 30px;

                        @include mobile {
                            width: 30px;
                            height: 30px;
                            padding: 5px;
                            overflow: hidden;
                        }
                    }
                }
            }

            .center {
                padding: 10px;
                border-radius: 10px;
                background-color: rgb(250, 244, 245);
                text-align: justify;
                overflow: hidden;

                @include mobile {
                    font-size: .7em;
                    padding-bottom: 30px;
                    margin: 2px;
                    overflow: hidden;
                }
            }

            .bottom {
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: center;
                overflow: hidden;
                //margin: 8px;

                h3 {
                    margin-bottom: 3px;
                    padding: 12px;
                    overflow: hidden;

                    @include mobile {
                        font-size: .8em;
                        //overflow: hidden;
                        //margin: 5px 5px;
                    }
                }

                h4 {
                    color: rgb(121, 115, 115);

                    @include mobile {
                        font-size: .7em;
                        overflow: hidden;
                        padding-bottom: 15px;
                    }
                }
            }
        }
    }
}